@charset "UTF-8";
.App {
  position: relative;
  display: flex;
  height: 100vh;
  margin: auto;
  font-family: '微軟正黑體'; }
  .App .lists-block {
    position: absolute;
    right: 0;
    width: 340px;
    height: calc(100% - 10px);
    box-sizing: border-box;
    padding: 10px;
    margin: 5px;
    border: 1px solid #002D42;
    border-radius: 5px; }
    .App .lists-block .settings {
      position: fixed;
      height: 160px; }
      .App .lists-block .settings button {
        border: 0;
        padding: 5px 10px;
        margin: 2px;
        border-radius: 8px;
        background-color: #acd6ff;
        font-family: '微軟正黑體';
        cursor: pointer; }
        .App .lists-block .settings button:focus, .App .lists-block .settings button:active {
          background-color: #2894ff;
          color: #fff; }
    .App .lists-block .content {
      position: relative;
      top: 160px;
      overflow: auto;
      height: calc(100% - 160px); }
