@charset "UTF-8";
.list-block {
  position: relative;
  display: block;
  margin: 10px 0;
  min-height: 100px;
  border: 1px solid #000;
  box-sizing: border-box;
  padding: 5px; }
  .list-block a, .list-block p {
    margin: 0;
    padding: 0; }
  .list-block i {
    margin: 2px; }
  .list-block:nth-child(odd) {
    background-color: #fff3ee; }
  .list-block:nth-child(even) {
    background-color: #fffcec; }
  .list-block .user-comment {
    display: inline-flex;
    justify-content: space-around;
    align-items: baseline;
    position: relative; }
    .list-block .user-comment .comment-price {
      margin-right: 10px;
      color: #d9b300; }
      .list-block .user-comment .comment-price:empty::before {
        content: '暫無價錢資訊';
        font-size: 12px; }
    .list-block .user-comment .comment-score {
      color: #01b468; }
    .list-block .user-comment .comment-count {
      font-size: 12px;
      color: #777; }
      .list-block .user-comment .comment-count:empty::before {
        content: '暫無評論資訊'; }
      .list-block .user-comment .comment-count:not(:empty)::before {
        content: '(';
        margin-left: 5px; }
      .list-block .user-comment .comment-count:not(:empty)::after {
        content: ')';
        margin-right: 5px; }
  .list-block .title {
    font-size: 18px;
    font-weight: bold;
    margin: 0; }
  .list-block .location i {
    color: #EA4335; }
  .list-block .location a {
    color: #0072e3;
    text-decoration: none; }
    .list-block .location a:hover, .list-block .location a:focus {
      text-decoration: underline; }
    .list-block .location a:empty::before {
      content: '暫無地址資訊'; }
